import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import ButtonBase from '@material-ui/core/ButtonBase';
import Divider from '@material-ui/core/Divider';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import { IconButton, withStyles } from "@material-ui/core";
import { useHistory } from "react-router-dom";
import { connect } from 'react-redux'
import PropTypes from 'prop-types'

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    margin: 'left',
    maxWidth: 450,
    paddingLeft: 10,
  },
  image: {
    width: 128,
    height: 128,
  },
  img: {
    margin: 'auto',
    display: 'block',
    maxWidth: '100%',
    maxHeight: '100%',
  },
  fonts: {
    fontSize: 20,
    fontStyle: 'italic',
    align: 'left',
    fontWeight: 'bold',
    color: 'darkgreen',
    paddingLeft: 18,

  },
  backArrow:
  {
    paddingLeft: 30,
  },
}));

function RecentlyAddedAudio() {
  const [recentlyAddedAudio, setRecentlyAddedAudio] = useState([]);
  const [setError] = useState(null);
  const classes = useStyles();
  const history = useHistory();

  useEffect(() => {
    fetch("http://ec2-13-232-236-83.ap-south-1.compute.amazonaws.com:8080/recently_added_books?book_type=AUDIO_BOOK")
      .then(res => res.json())
      .then(
        (result) => {
          setRecentlyAddedAudio(result);
          console.log(result)

        },
        (error) => {
          setError(error);
        }
      )
  }, [])

  const handleRoute = (route) => {
    history.push(`${route}`)
  };

  return (
    <div>
      <IconButton className={classes.backArrow} aria-label="delete">
        <ArrowBackIcon onClick={() => handleRoute('/home')} />
      </IconButton>
      <text className={classes.fonts}>Recently Added Audio Books</text>
      <Divider variant="middle" />
      <ul>
        {recentlyAddedAudio.map(item => (
          <div className={classes.root}>
            <Paper className={classes.paper}>
              <Grid container spacing={2}>
                <Grid item xs={12} sm container>
                  <Grid item>
                    <ButtonBase className={classes.image} >
                      <img
                        className={classes.img} alt="complex" src={item.thumbnail_url}
                        onClick={
                          () =>
                            handleRoute(`/audiobook/${item.file_name}/${item.title}`)
                        }
                      />
                    </ButtonBase>
                  </Grid>

                  <Grid item xs container direction="column" spacing={2}>
                    <Grid item xs>
                      <Typography gutterBottom variant="subtitle1">
                        {item.title}
                      </Typography>
                      <Typography variant="body2" gutterBottom>
                        Author: {item.author}
                      </Typography>
                      <Typography variant="body2" color="textSecondary">
                        Category: {item.category}<br />
                  Audio time: {item.total_audio_time}
                      </Typography>
                    </Grid>

                  </Grid>
                </Grid>
              </Grid>
            </Paper>
          </div>
        ))}
      </ul>
    </div>
  )
}

const mapStateToProps = () => {
  return {}
}

const mapDispatchToProps = () => {
  return {
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(useStyles)(RecentlyAddedAudio))

RecentlyAddedAudio.propTypes = {
  recentlyAddedAudio: PropTypes.object,
  fileName: PropTypes.any,
}